@layer components {
	/* wc-block-featured-product */
	/* wc-block-featured-category */
	.wc-block-featured-product,
	.wc-block-featured-category {
		@apply relative overflow-hidden rounded-media my-12 lg:my-24;

		&__wrapper {
			@apply relative max-w-4xl lg:px-9;
		}

		&__title {
		}
		&__description {
		}

		&__price {
			@apply price !important;

			.alignnone & {
				@apply justify-center;
			}
			.has-left-content & {
				@apply justify-start;
			}
			.has-right-content & {
				@apply justify-end;
			}
		}

		&__link {
			@apply mt-3;

			.wp-block-button {
				&.alignright {
					@apply text-right;
				}
			}

			.wp-block-button__link {
				@apply button;
			}
		}
	}

